import React,{useState} from 'react'
import "./login.css";
import {Form,FormGroup,Button,Label,Input} from "reactstrap";
import axios from "axios";
import { useHistory } from "react-router-dom";
function Login() {

    const [tc,setTc] = useState("");
    const [password,setPassword] = useState("");
    const history = useHistory();

    const goRegister = () =>{
        history.push("/register");
    }
    
    const goStaffLogin = () =>{
        history.push("/staffLogin");
    }
    
    const goAdminLogin = () =>{
        history.push("/adminLogin");
    }




    const handleSubmit = (evt) => {
        evt.preventDefault();
        const loginInfo = {
          tc,
          password,
        };
        axios.post('/api/v1/login', loginInfo)
        .then(response => {
            localStorage.setItem("id", JSON.stringify(response.data));
            history.push("/user")
        
        })
        .catch(error => {
            console.error('There was an error!', error);
        });
      }


    return (
        <div className="compDiv">
        <h2>Sign In</h2>
        <Form className="form" onSubmit={handleSubmit}>
          <FormGroup>
            <Label for="tc">TC Number</Label>
            <Input
              type="text"
              name="tc"
              id="tc"
              placeholder="Enter Tc Number"
              value={tc}
              onChange={e => setTc(e.target.value)}
            />
          </FormGroup>
          <FormGroup>
            <Label for="password">Password</Label>
            <Input
              type="password"
              name="password"
              id="password"
              placeholder="Enter Password"
              value={password}
              onChange={e => setPassword(e.target.value)}
              
            />
          </FormGroup>
          <Button type="submit" color="primary">Log In</Button>
          <Button type="submit" color="dark" className="mt-2" onClick={goRegister}>Register</Button>
          <Button type="submit" color="success" className="mt-2" onClick={goStaffLogin}>Staff Log In</Button>
          <Button type="submit" color="secondary" className="mt-2" onClick={goAdminLogin}>Admin Log In</Button>



      </Form>
      
    </div>
    )
}

export default Login
